.timeline {
	border-left: 3px solid #162B5F;
	border-bottom-right-radius: 4px;
	border-top-right-radius: 4px;
	background: rgba(114, 138, 245, 0.09);
	margin: 0 auto;
	letter-spacing: 0.2px;
	position: relative;
	line-height: 1.4em;
	font-size: 1.03em;
	padding: 50px;
	list-style: none;
	text-align: left;
	max-width: 40%;
	padding: 25px;
	max-width: 98%;
}

.timeline h1 {
	font-weight: 300;
	font-size: 1.4em;
}

.timeline h2,
.timeline h3 {
	font-weight: 600;
	font-size: 1rem;
	margin-bottom: 10px;
}

.timeline .event {
	border-bottom: 1px dashed #e8ebf1;
	padding-bottom: 25px;
	padding-top: 30px;
	margin-bottom: 25px;
	position: relative;
}

.timeline .event:last-of-type {
	padding-bottom: 0;
	margin-bottom: 0;
	border: none;
}

.timeline .event:before,
.timeline .event:after {
	position: absolute;
	display: block;
	top: 0;
}

.timeline .event:before {
	left: 0px;
	content: attr(data-date);
	text-align: left;
	font-weight: 100;
	font-size: 0.9em;
	min-width: 120px;
}

.timeline .event:after {
	-webkit-box-shadow: 0 0 0 3px #162B5F;
	box-shadow: 0 0 0 3px #162B5F;
	left: -31.8px;
	background: #fff;
	border-radius: 50%;
	height: 9px;
	width: 9px;
	content: "";
	top: 5px;
}

.rtl .timeline {
	border-left: 0;
	text-align: right;
	border-bottom-right-radius: 0;
	border-top-right-radius: 0;
	border-bottom-left-radius: 4px;
	border-top-left-radius: 4px;
	border-right: 3px solid #162B5F;
}

.rtl .timeline .event::before {
	left: 0;
	right: -170px;
}

.rtl .timeline .event::after {
	left: 0;
	right: -55.8px;
}